.button {
    border-style: none;
    border-radius: 10px;
    background-color: hsl(19, 68%, 58%);
    width: 200px;
    padding: 4px;
    transition: 1s;
  }
  
  .button:hover {
    background-color: hsl(19, 68%, 53%)
  }
  
  .button:active {
    background-color: hsl(19, 68%, 45%)
  }